.body {
  font-family: 'Inter', sans-serif;
  background-color: #FDFEFF;
}
.main {
  padding-bottom: 50px;
}
ul {
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}
img {
  object-fit: contain;
}
li {
  list-style: none;
}
.container {
  max-width: 1600px;
  width: 100%;
  margin: 0 auto;
}
@media (max-width: 1650px) {
  .container {
    padding: 0 20px 0 20px;
  }
}
.visually__hidden {
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.visually__hidden--reset {
  width: unset;
  height: unset;
  margin: -unset;
  border: unset;
  padding: unset;
  white-space: unset;
  clip-path: unset;
  clip: unset;
  overflow: unset;
}
.menu__name {
  font-size: 28.039px;
  font-weight: 800;
  color: #343434;
  font-family: 'Roboto Slab', serif;
}
@media (max-width: 1650px) {
  .menu__name {
    font-size: 20px;
  }
}
.menu__list {
  display: flex;
}
.menu__item {
  margin-left: 10px;
}
.menu__item:first-child {
  margin-left: 0;
}
.menu {
  padding: 60px 0 111px 0;
}
@media (max-width: 1650px) {
  .menu {
    padding: 15px 0 25px 0;
  }
}
.menu__link {
  text-decoration: none;
}
.menu__link {
  display: flex;
  align-items: center;
}
.menu__svg {
  margin-right: 16px;
}
.general__list--row {
  display: flex;
}
@media (max-width: 1650px) {
  .general__list--row {
    flex-direction: column;
  }
}
.general__item {
  margin-left: 165px;
  flex: 1;
}
.general__item:first-child {
  margin-left: 0;
}
@media (max-width: 1650px) {
  .general__item {
    margin-left: 0;
    display: flex;
    flex-direction: column;
  }
}
.general__svg {
  width: 600px;
  height: 565px;
  margin-left: 50px;
  margin-top: 35px;
}
@media (max-width: 1650px) {
  .general__svg {
    width: 100%;
    height: 100%;
    margin-left: 0;
    margin-top: 0;
  }
}
.general__list {
  display: flex;
}
@media (max-width: 1650px) {
  .general__list {
    display: grid;
    gap: 30px;
  }
}
.heading__h1 {
  font-family: 'Roboto Slab', serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 110%;
  margin: 35px 0 60px;
}
@media (max-width: 1650px) {
  .heading__h1 {
    font-size: 25px;
    margin: 15px 0 50px;
  }
}
.frame__label {
  font-size: 16px;
  font-weight: 400;
  font-family: 'Inter', sans-serif;
}
.frame__input {
  font-family: 'Inter', sans-serif;
  border: 1.25px solid #EAEAEA;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 400;
  width: 100%;
  padding: 17.5px 15px 17.5px 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.frame__input::placeholder {
  color: #9A9A9A;
}
.frame__input:focus {
  outline: 1.25px solid #5CBEFE;
}
.frame__item {
  position: relative;
  margin-left: 15px;
}
.frame__item:first-child {
  margin-left: 0;
}
@media (max-width: 1650px) {
  .frame__item {
    margin-left: 0;
  }
}
.frame__width {
  width: 100%;
}
.frame__label {
  position: absolute;
  z-index: 1;
  top: -7px;
  left: 22px;
  background-color: #FFFFFF;
}
.frame__input--placeholder {
  display: flex;
  align-items: center;
  color: #9A9A9A;
  background-color: transparent;
  cursor: pointer;
  width: 125px;
  height: 100%;
}
@media (max-width: 1650px) {
  .frame__input--placeholder {
    width: 100%;
    min-height: 57px;
  }
}
.frame__dropdown {
  opacity: 0;
  pointer-events: none;
  height: 0;
}
.frame__dropdown--js {
  opacity: 1;
  pointer-events: all;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 10rem;
  overflow-y: scroll;
  overflow-x: hidden;
  background-color: #FFFFFF;
  border-radius: 12px;
  border: 1.25px solid #EAEAEA;
  box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.12);
}
.frame__input--choose {
  color: #343434;
}
.frame__wrapper {
  position: absolute;
  top: 15px;
  right: 15px;
  pointer-events: none;
}
[data-dropdown="button"][aria-expanded="true"] + .frame__wrapper {
  transition: 0.5s ease;
  transform: rotate(180deg);
}
[data-dropdown="button"][aria-expanded="false"] + .frame__wrapper {
  transition: 0.5s ease;
  transform: rotate(0deg);
}
.frame__close {
  cursor: pointer;
  border: none;
  background-color: transparent;
  position: absolute;
  right: 0;
  top: 15px;
  display: none;
}
.frame__textarea {
  resize: none;
  white-space: unset;
  overflow: unset;
  text-overflow: unset;
}
.frame__status {
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.dropdown__link {
  text-decoration: none;
  color: #343434;
}
.dropdown__item {
  padding: 15px;
  position: relative;
}
.dropdown__svg {
  background-image: url("../img/tick-circle.png");
  background-repeat: no-repeat;
  width: 20px;
  height: 20px;
  display: block;
  position: absolute;
  top: 10px;
  right: -20px;
}
.dropdown__active {
  font-weight: 600;
}
.hints {
  margin-top: 20px;
}
.hints__radio {
  display: none;
}
.hints__sign {
  padding: 10px 0 10px 0;
  background-color: #EEEFF0;
  cursor: pointer;
  border-radius: 100px;
  padding: 10px 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hints__sign:hover {
  transition: 0.5s ease;
  background-color: #343434;
  color: #FFFFFF;
}
@media (max-width: 1650px) {
  .hints__sign {
    min-width: 235px;
  }
}
.hints__list {
  display: flex;
  flex-wrap: wrap;
  min-width: 800px;
  scrollbar-color: #343434;
  scrollbar-width: thin;
}
@media (max-width: 1650px) {
  .hints__list {
    min-width: unset;
    height: 150px;
    overflow-x: hidden;
    overflow-y: scroll;
  }
}
.hints__list::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #EEEFF0;
}
.hints__item {
  padding: 10px;
  display: flex;
}
.hints__button {
  cursor: pointer;
  border: none;
  background-color: transparent;
  display: flex;
  flex-direction: row-reverse;
}
.hints__caption {
  font-size: 18px;
  font-weight: 500;
  font-family: 'Inter', sans-serif;
  color: #3CA9F0;
}
[data-hints="hidden"] {
  display: none;
}
.hints__item--visible {
  display: flex;
}
.textarea__item {
  position: relative;
}
.textarea__item:first-child {
  margin-top: 20px;
}
.textarea__item--flex {
  display: flex;
  justify-content: flex-end;
}
.textarea__wrapper {
  font-size: 14px;
  line-height: 21px;
  font-family: 'Montserrat', sans-serif;
  margin-right: 20px;
}
.input__error {
  color: red;
  position: absolute;
}
.invalid__field {
  border: 1px solid red;
}
.group-button__button {
  background-color: transparent;
  cursor: pointer;
  border: none;
  padding: 18px 0 18px 0;
  width: 100%;
  border-radius: 12px;
}
.group-button__list {
  display: flex;
}
@media (max-width: 1650px) {
  .group-button__list {
    display: grid;
    gap: 30px;
    margin-top: 30px;
  }
}
.group-button__button--blue {
  min-width: 235px;
}
.group-button__button--transparent {
  border: 1px solid #3CA9F0;
  min-width: 159px;
}
.group-button__sign {
  font-family: 'Inter', sans-serif;
  font-size: 18px;
  font-weight: 500;
}
.group-button__sign--blue {
  color: #3CA9F0;
}
.group-button__sign--white {
  color: #FFFFFF;
}
.group-button__button--blue {
  background-color: #3CA9F0;
  box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.12);
}
.group-button__item {
  margin-left: 15px;
}
.group-button__item:first-child {
  margin-left: 0;
}
[data-button="submit"]:disabled {
  background-color: #EEEFF0;
  cursor: not-allowed;
}
